.pricing-container {
  background-color: #050505;
  width: 100vw;
  color: white;
  position: relative;
  text-align: center;
  // padding: 1.25rem; /* 20px */
  box-sizing: border-box;
  overflow: hidden; /* Prevent overflow */

  .networking {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 3.75rem; /* 60px */
    padding: 2rem 0;

    @media (max-width: 460px) {
      flex-direction: column;
      gap: 1rem;
    }
  
    .image {
      width: 50%; /* Make the image take half the screen */
      display: flex;
      justify-content: flex-start; /* Align the image to the left */

      @media (max-width: 460px) {
        width: 80%;
      }

      img {
        width: 100%; /* Ensure the image fills its container */
        height: auto;
      }
    }
  
    .net-platform {
      width: 50%; /* Make the text area take half the screen */
      display: flex;
      flex-direction: column;
      align-items: flex-start; /* Align content to the left */
      justify-content: center;

      @media (max-width: 460px) {
        width: 80%;
        align-items: center;
      }
  
      h1 {
        text-align: left; /* Keep text aligned to the left */
        font-family: Nunito;
        font-size: 2.75rem; /* 44px */
        font-weight: 700;
        line-height: 3.625rem; /* 58px */
        letter-spacing: -0.015em;
        @media (max-width: 460px) {
          text-align: center;
          font-size: 32px;
        }
      }
  
      p {
        text-align: left; /* Keep the paragraph aligned to the left */
        font-family: Outfit;
        font-size: 1.25rem; /* 20px */
        width: 90%;
        font-weight: 400;
        line-height: 3.125rem; /* 50px */
        @media (max-width: 460px) {
          text-align: center;
          width: 80%;
          line-height: 30px;
        }
      }
      button {
        display: block;
        margin-top: 1.25rem; /* 20px */
        background-color: #fd5901;
        padding: 0.625rem; /* 10px */
        border-radius: 1.25rem; /* 20px */
      }
    }
  }
  

  .heading {
    font-size: 2rem; /* 32px */
    font-weight: 500;
    font-family: Outfit;

    h1 {
      margin-top: 2.5rem; /* 40px */
    }

    h3 {
      font-size: 1.375rem; /* 22px */
      font-weight: 300;
      line-height: 1.875rem; /* 30px */
      text-align: center;
      margin: 1.25rem auto; /* 20px */
      width: 60%;
    }

    @media (min-width: 768px) {
      font-size: 2.5rem; /* 40px */
    }
  }

  .background-img {
    position: absolute;

    &.dot1 {
      top: 2.8125rem; /* 45px */
      left: 0.625rem; /* 10px */
      width: 3.125rem; /* 50px */

      @media (min-width: 768px) {
        left: 6.25rem; /* 100px */
        width: 4.375rem; /* 70px */
      }
    }

    &.small {
      top: 5.9375rem; /* 95px */
      left: 80%;
      width: 1.875rem; /* 30px */

      @media (min-width: 768px) {
        left: 85%;
        width: 3.125rem; /* 50px */
      }
    }

    &.dot2 {
      top: 18.4375rem; /* 295px */
      left: 85%;
      width: 3.125rem; /* 50px */

      @media (min-width: 768px) {
        left: 90%;
        width: 4.375rem; /* 70px */
      }
    }

    &.blur-dot1 {
      top: 25rem; /* 400px */
      left: 0.3125rem; /* 5px */
      width: 5rem; /* 80px */

      @media (min-width: 768px) {
        left: 0.625rem; /* 10px */
        width: 6.25rem; /* 100px */
      }
    }

    &.blur-dot2 {
      top: 31.25rem; /* 500px */
      left: 60%;
      width: 5rem; /* 80px */

      @media (min-width: 768px) {
        left: 65%;
        width: 6.25rem; /* 100px */
      }
    }

    &.blur-dot3 {
      top: 43.125rem; /* 690px */
      left: 20%;
      width: 5rem; /* 80px */

      @media (min-width: 768px) {
        left: 30%;
        width: 6.25rem; /* 100px */
      }
    }

    &.bg {
      top: -18.75rem; /* -300px */
      width: 100%;
      z-index: 0 !important;
    }
  }
}
